@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700;800;900&display=swap);
:root {
    --primary: #fff;
  }
  
  .btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
  }
  
  .btn--primary {
    background-color: #fff;
    background-color: var(--primary);
    color: #242424;
    font-weight: bold;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
  }
  
  .btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
  }
  
  .btn--medium {
    padding: 8px 20px;
    font-size: 18px;
  }
  
  .btn--large {
    padding: 12px 26px;
    font-size: 20px;
  }
  
  .btn--large:hover,
  .btn--medium:hover {
    transition: all 0.3s ease-out;
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
.navbar {
  background: var(--bg-color-1);
  display: flex;
  height: 80px;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1500px;
}

.navbar-logo {
  color: var(--font-color-1);
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fas {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  align-items: center;
  margin-right: 2rem;
}

.nav-item {
  color: var(--font-color-1);
  margin: 0.5rem;
  padding: 0.5rem;
  font-weight: bold;
  border-bottom: 4px solid transparent;
}

.nav-item:hover {
  border-bottom: 4px solid rgb(52, 4, 4);
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.fa-bars {
  color: var(--font-color-1);
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    position: absolute;
    top: 80px;
    right: -100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    transition: all 0.5s ease;
    width: auto;
    padding: 0px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }
  .nav-menu.active {
    background: var(--bg-color-2);
    right: 0;
    opacity: 1;
    z-index: 1;
  }

  .nav-item {
    color: var(--font-color-1);
    margin: 0rem;
    width: 100%;
    padding: 2px 20px;
    font-weight: bold;
    border-bottom: 4px solid transparent;
    transition: all 0.3s ease-out;
  }

  .nav-item:hover {
    color: var(--font-color-1);
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: var(--font-color-1);
    font-size: 2rem;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: all 0.3s ease-out;
  }
}

:root {
  --bg-color-1: rgb(255, 255, 255, 0.8);
  --bg-color-2: #ee6728c9;
  --font-color-1: black;
  --font-color-2: rgb(158, 67, 6);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  text-decoration: none;
  transition: all 0.2s linear;
}
html {
  /* font-size:67.5%; */
  scroll-padding-top: 80px;
  scroll-behavior: smooth;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  font-family: "Nunito", sans-serif;
}
.container {
  margin: 2rem 0;
}

.section-title {
  font-size: 2rem;
  text-align: center;
  color: #ffa500;
  text-shadow: -2px 0 black;
  text-transform: capitalize;
  padding: 2rem 0;
}

.hero-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("https://source.unsplash.com/FHon8lLKibM");
  background-position: center center;
  background-size: cover;
  object-fit: cover;
  margin-top: 1rem;
  background-color: violet;
}
.images {
  background-color: rgba(246, 136, 96, 0.452);
  width: 500px;
  /* height: 400px; */
  margin: 1rem;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  border-radius: 24% 76% 26% 74% / 100% 0% 100% 0%;
}
.images img {
  height: 600px;
  object-fit: contain;
}
.hero-description {
  text-align: center;
  width: 500px;
  padding: 20px;
  margin: 20px;
}
.hero-text {
  padding: 20px 0;
  line-height: 1.5;
}
.btn {
  padding: 15px 40px;
  color: #fff;
  background-color: #222;
  font-size: 1.5rem;
  margin: 10px 2px;
  overflow: hidden;
  border-radius: 0% 37% 0% 39% / 40% 45% 32% 47%;
}
.btn:hover {
  background-color: rgb(243, 58, 25);
}

@media screen and (max-width: 960px) {
  .hero-container {
    flex-direction: column-reverse;
  }
  .hero-description {
    width: auto;
  }
}

.services-strap {
  background-color: var(--bg-color-one);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin: 0.3rem 0;
}
.service {
  padding: 0.5rem;
  min-width: 200px;
  text-align: center;
  flex: 1 1;
}
.service i {
  font-size: 3rem;
  padding: 10px;
  background-color: white;
  -webkit-clip-path: ellipse(50% 49% at 50% 50%);
          clip-path: ellipse(50% 49% at 50% 50%);
}
.service h2 {
  text-transform: capitalize;
}

@media screen and (max-width: 960px) {
  .hero-container {
    flex-direction: column-reverse;
  }
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  /* flex: 1; */
  max-width: 400px;
  text-align: center;
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  overflow: hidden;
  border-radius: 5px;
  margin: 0.5rem;
}
.card:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card .icons {
  position: absolute;
  top: 6px;
  left: 4px;
  display: flex;
  /* flex-direction: column; */
  text-align: left;
}
.card .icons i {
  font-size: 1.5rem;
  color: rgb(26, 18, 8);
  padding: 4px;
  cursor: pointer;
}
.card .icons i:hover {
  color: red;
}
.card .image {
  max-height: 80%;
  overflow: hidden;
}
.card img {
  min-width: 400px;
  /* max-width: 100%; */
  height: 250px;
  object-fit: cover;
  transition: all 1s ease-in-out;
}
.card:hover > .image img {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  opacity: 0.5;
}
.card h2 {
  font-size: 20px;
  text-transform: capitalize;
}
.card span {
  font-size: 22px;
  font-weight: bold;
  padding: 0 2rem;
}
.card .previous {
  text-decoration: line-through;
}
.card .description {
  padding: 1rem;
}

.compliment-wrapper {
  display: flex;
  width: 100vw;
  background-color: blanchedalmond;
}

.compliment-wrapper img {
  flex: 1 1;
  width: 500px;
  margin: 0 auto;
  padding: 0 20px;
}

.compliment-wrapper .writings {
  flex: 1 1;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 960px) {
  .compliment-wrapper {
    flex-direction: column;
  }
}

.footer-wrapper {
  background-color: black;
  display: flex;
  width: 100vw;
  color: white;
  padding: 1rem;
  font-size: 1rem;
  flex-wrap: wrap;
}

.footer-wrapper .logo {
  flex: 1 1;
  padding: 0.8rem;
  font-size: 2rem;
  color: #f1356d;
}

.footer-wrapper .links {
  flex: 1 1;
  min-width: 300px;
}

.footer-wrapper ul {
  padding: 0;
}

.footer-wrapper .links li {
  text-decoration: none;
  list-style: none;
  cursor: pointer;
  padding: 0.2rem;
}
.footer-wrapper .links li:hover {
  color: #f1356d;
}

.create {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.copy-right {
  text-transform: capitalize;
  text-align: center;
}
@media screen and (max-width: 960px) {
  .footer-wrapper {
    flex-direction: column;
  }
}

